<template lang="pug">

.layout-main
    c-header
    router-view.page(v-slot="{ Component, route }")
        transition(name="fade" mode="out-in" @before-enter="loader = true" @before-leave="loader = false")
            component(:is="Component")
    //- transition(name="fade")
    //-     .loader(v-if="loader")
    c-footer
    c-popup

</template>
<script>
import cHeader from 'components/header'
import cFooter from 'components/footer'
import cPopup from 'components/popups'
export default {
    data: () => ({
        loader: false
    }),
	computed: {
		disableScroll() {
			return this.$store.state.ui.disableScroll
		},
		popup() {
			return this.$store.getters['ui/popup'].name
		},
        editor() {
            return this.$store.state.content.editor
        },
	},
	components: { cHeader, cFooter, cPopup }
}
</script>
<style lang="stylus" scoped>

.layout-main
    min-height 100vh
    display flex
    flex-direction column
    padding-top 60px
    +$phone()
        padding-top 55px


.page
    &.fade-enter-active, &.fade-leave-active
        transition: opacity .25s ease
    &.fade-enter-from, &.fade-leave-to
        opacity 0


.loader
    position absolute
    z-index 30
    top 50vh
    left 50%
    margin-top -50px
    margin-left -30px
    width 60px
    height 60px
    border 4px solid $gray
    border-bottom-color transparent
    border-radius 100%
    animation spin .8s infinite linear
    transition opacity .3s ease
    opacity 0
    &.fade-enter-from, &.fade-leave-to
        opacity 0

@keyframes spin
    0%
        transform rotate(0deg)
    100%
        transform rotate(360deg)


</style>
